.ring {
    display: inline-block;
    position: fixed;
    top: 50vh;
    left: 50vw;
    width: 80px;
    height: 80px;
    z-index: 101;
    img {
        animation: 1s linear 0s normal none infinite running ring;
        -webkit-animation: 1s linear 0s normal none infinite running ring;
        width: 100px;
    }
    @keyframes ring {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
    @-webkit-keyframes ring {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}



